ómico Autorizado), Sistema Nacional de Competitividad, Ciencia, Tecnología e Innovación (SNCCTl), “Exporta Fácil” y los promovidos por Procolombia para exportadores, entre otros (OECD/CAF, 2019). Como producto de este trabajo conjunto entre instituciones y empresas, tan solo en año 2017, se recibió información de 24.993 nuevas posibilidades de ventas con 7.006 compradores internacionales (Procolombia, 2018). Igualmente, producto de la inversión extrajera, en el año 2017, se generaron 32.507 empleos en los proyectos iniciados (Procolombia, 2018)."),_c('br'),_c('br'),_vm._v("Finalmente, desde hace cinco décadas, se crearon en Colombia las “Zonas Francas” como instrumentos del comercio exterior y generadoras de empleo. Hoy, en todo el mundo existen 1.245 ZF que han generado 49.292.231 empleos y tienen 516.399 empresas en las actividades comercial e industrial (Portafolio, 2019). Así mismo, las exportaciones de mercancías desde las zonas francas registraron un incremento de 21,5% al pasar de US$225,4 millones FOB en agosto de 2018 a US$273,7 millones FOB en el mismo mes de 2019 (DANE, 2019), múltiples razones por las cuales se hace necesario el desarrollo de la Tecnología en Procesos Para La Negociación Internacional.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container tarjeta tarjeta__template--azul-claro p-4 p-md-5 mb-5"},[_c('div',{staticClass:"titulo__template--a mb-4"},[_c('span',{staticClass:"h4"},[_vm._v("3. Competencias"),_c('br'),_vm._v("a desarrollar")])]),_c('ul',{staticClass:"lista-ul px-3"},[_c('li',[_c('i',{staticClass:"lista-ul__vineta"}),_vm._v("Recolectar información de acuerdo con metodología de investigación")]),_c('li',[_c('i',{staticClass:"lista-ul__vineta"}),_vm._v("Formular el plan según objetivos y metodologías")]),_c('li',[_c('i',{staticClass:"lista-ul__vineta"}),_vm._v("Planear la operación de importación y exportación de bienes según normativa de comercio exterior")]),_c('l